"use strict";

// swiperslider
const swiper = new Swiper(".swiper-container", {
    //loop: true,
    navigation: {
        nextEl: ".swiper-button-next",
        prevEl: ".swiper-button-prev"
    },
    //  
    pagination: {
        el: ".swiper-pagination",
        type: "bullets",
        clickable: "clickable"
    },
    //  
    scrollbar: {
        el: ".swiper-scrollbar",
        hide: true,
        draggable: false
    },

    centeredSlides: true, //
    //speed: 2000,
    //effect: "slide",
    //effect: "fade",
    //effect: "cube",
    effect: "coverflow",
    //effect: "flip",
    //effect: "cards",
    //effect: "creative",

    spaceBetween: 16, //16px
    slidesPerView: 1, //2
    breakpoints: { //
        // 599px
        599: {
            slidesPerView: 2, //2
        },
        // 1024px
        1024: {
            slidesPerView: 3, //3
        },
    },
    autoplay: {
        delay: 3000,
        disableOnInteraction: false,
    }
});