import buttonHumburger from './content/btn_humburger'; //  
import partsHeader from './content/header'; //  
import displayLoader from './content/disp_loader'; //  
import contactForm from './content/contactform'; //  
import pageBackGround from './content/pagebackground'; //  
import osCheck from './content/oscheck'; //  OS
import adjustViewport from './adjustviewport'; //  
import myExternalLinks from './myexternallinks'; //  
import eeParallax from './eeparallax/eeparallax'; //  
